import interfaceurl from '@/utils/interface.js'
export default {
	data() {
		return {
			addressList: [],
			origin: 1
		}
	},
	onLoad(options) {
	  if(options.origin) {
		this.origin = options.origin
	  }
	},
	onShow() {
		this.getAddressList()
	},
	methods: {
		getAddressList() {
			let that = this
			let params = {
				page: 1,
				size: 10
			}
			interfaceurl.checkAuth(interfaceurl.addressPageList, params, false).then((res) => {
				that.addressList = res.data.data
			});
		},
		// selectAddress(item) {
		// 	if(this.origin == 1){
